import React,{ useState }from 'react';

import Aux from '../Aux/Aux';
import Toolbar from '../../Components/Navigation/Toolbar/Toolbar';
import SideDraw from '../../Components/Navigation/SideDraw/SideDraw';
import './Layout.css';

const Layout =(props) =>{
    let [sidedrawstate,setsidedraw]= useState({
        sidedraw:false
    })

    let sidedrawClosedHandler = () =>{
        setsidedraw({
            sidedraw:false
        })
    }

    let sidedrawToggleHandler = () =>{
        setsidedraw((prevState) => {return({sidedraw:!prevState.sidedraw})})
    }

    return(
        <Aux>
            <SideDraw open={sidedrawstate.sidedraw} closed={sidedrawClosedHandler}  />
            <Toolbar menu={sidedrawToggleHandler}/>
            <main className="Content">
                {props.children}
            </main>
        </Aux>
    )
}

export default Layout;


