import React,{Component} from 'react';

import Aux from '../../hoc/Aux/Aux';
import Burger from '../../Components/Burger/Burger';
import BuilderControls from '../../Components/Burger/BuildControls/BuildControls';
import Modal from '../../Components/UI/Modal/Modal';
import OrderSummary from '../../Components/Burger/OrderSummary/OrderSummary';
import axios from '../../axios-order';
import Spinner from '../../Components/UI/Spinner/Spinner';

const INGREDIENT_PRICE = {
    salad:20,
    cheese:30,
    bacon:60,
    meat:80
}

class BurgerBuilder extends Component{

    state={
        ingredients:{
            salad:0,
            bacon:0,
            cheese:0,
            meat:0
        },
        totalPrice:20,
        purchaseable:false,
        purchasing:false,
        loading:false
    }

    updatePurchaseState = (ingredients) =>{
        console.log('updatepurchasestate   :',ingredients)
        const sum = Object.keys(ingredients)
            .map(igKeys =>{
                return ingredients[igKeys]
            })
            .reduce((sum,el)=>
                {return sum = sum+el;}
            ,0);
            this.setState({purchaseable: sum>0})
    }   

    addIngredientHandler = (type) =>{
        const oldCount = this.state.ingredients[type];
        const updatedCount = oldCount + 1;
        const updatedIngredients ={
            ...this.state.ingredients
        };
        updatedIngredients[type]=updatedCount;
        const ingredientsPrice=INGREDIENT_PRICE[type];
        const oldPrice = this.state.totalPrice;
        const newPrice = oldPrice + ingredientsPrice;

        this.updatePurchaseState(updatedIngredients)

        this.setState({totalPrice:newPrice,ingredients:updatedIngredients});
    }

    removeIngredientHandler = (type) =>{
        const oldCount = this.state.ingredients[type];
        if(oldCount <= 0){
            return
        }
        const updatedCount = oldCount - 1;
        const updatedIngredients ={
            ...this.state.ingredients
        };
        updatedIngredients[type]=updatedCount;
        const ingredientsPrice=INGREDIENT_PRICE[type];
        const oldPrice = this.state.totalPrice;
        const newPrice = oldPrice - ingredientsPrice;
        this.updatePurchaseState(updatedIngredients)

        this.setState({totalPrice:newPrice,ingredients:updatedIngredients});
    }

    purchaseHandler = () =>{
        this.setState({
            purchasing : true
        })
    }
    purchaseCancelHandler = () =>{
        this.setState({
            purchasing : false
        })
    }

    purchaseContinueHandler = () =>{
        alert('Your Order has been placed.........');
        this.setState({
            loading:true
        })
        const order={
            ingredients:this.state.ingredients,
            price:this.state.totalPrice,
            customer:{
                name:'thahseen',
                mobNum:'321456789',
                address:{
                    street:'potato street',
                    area:'potato area',
                    zipcode:600093
                }
            }
        }

        axios.post('/oders.json',order)
                .then(response => {this.setState({loading:false,purchasing:false})})
                .catch(error => {this.setState({loading:false,purchasing:false})})
    }

    render(){

        const disabledInfo ={
            ...this.state.ingredients
        };

        for(let key in disabledInfo){
            disabledInfo[key]= disabledInfo[key]<=0
        }

        let orderSummary =<OrderSummary 
            ingredients={this.state.ingredients}
            purchaseCancelled={this.purchaseCancelHandler}
            purchaseContinued={this.purchaseContinueHandler}
            finalprice={this.state.totalPrice}/>

        if(this.state.loading){
            orderSummary=<Spinner/>
        }

        return(
            <Aux>
                <Modal show={this.state.purchasing} modalClosed={this.purchaseCancelHandler}>
                    {orderSummary}
                </Modal>
                <Burger ingredients={this.state.ingredients} />
                <BuilderControls 
                    price={this.state.totalPrice}
                    ingredientsAdded={this.addIngredientHandler}
                    ingredientsRemoved={this.removeIngredientHandler}
                    disabled={disabledInfo}
                    purchase={this.state.purchaseable}
                    ordered={this.purchaseHandler}
                />
            </Aux>
        );
    }
} 

export default BurgerBuilder;


