import React from "react";
import './Logo.css'
import Logo from '../../assets/images/burger-logo.png';

const logo =()=>{
    return (
        <div className='Logo'>
            <img src={Logo} alt='MyBurger_logo'/>
        </div>
    )
}

export default logo;


